

:root {
    --orange-LR: linear-gradient(to right, rgb(248, 132, 0), #d86800af);
    --blue-RL: linear-gradient(to left, rgb(248, 132, 0), #994a01);
    
}
h1 {
    font-family: 'Courier New', Courier, monospace;
}

#cv-pexel {
    height: 70rem;
    width: 70rem;
    
}
.profileIcons {
    width: 2rem;
    height: 2rem;
}
.translateBtn {
    color: grey;
    width: 50px;
    height: 25px;
    transition: all 0.1s;
    border-style: solid;
    border-radius: 5px;
}
.translateBtn:hover {
    border-color: lightgray;
}

.translateBtn:active {
    height: 20px;
    width: 45px;
}
